import { Component, EventEmitter, Output } from '@angular/core';

@Component({
  moduleId: 'module.id',
  selector: 'search',
  template: require('./search.component.html')
})
export class SearchComponent {

  @Output() searchReq = new EventEmitter();
  search = {};

  constructor(){
  }

  onSearch(){
    this.searchReq.emit(this.search);
  }

}



